/* Using Tutorial: https://codepen.io/nxworld/pen/OyRrGy by Naoya */

.scroll-arrow a {
  position: absolute;
  bottom: 10%;
  left: 50%;
  z-index: 2;
  display: inline-block;
  transform: translate(0, -50%);
  -o-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
  color: #fff;
  transition: opacity .3s;
  -o-transition: opacity .3s;
  -ms-transition: opacity .3s;
  -moz-transition: opacity .3s;
  -webkit-transition: opacity .3s;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  animation: arrow-anim 2s infinite;
  -o-animation: arrow-anim 2s infinite;
  -ms-animation: arrow-anim 2s infinite;
  -moz-animation: arrow-anim 2s infinite;
  -webkit-animation: arrow-anim 2s infinite;
  box-sizing: border-box;
}
.scroll-arrow a:hover {
  opacity: .5;
  cursor: pointer;
}

/********************************* arrow-anim *********************************/

@keyframes arrow-anim {
  0% {
    -webkit-transform: rotate(-45deg) translate(0, 0);
            transform: rotate(-45deg) translate(0, 0);
  }
  20% {
    -webkit-transform: rotate(-45deg) translate(-10px, 10px);
            transform: rotate(-45deg) translate(-10px, 10px);
  }
  40% {
    -webkit-transform: rotate(-45deg) translate(0, 0);
            transform: rotate(-45deg) translate(0, 0);
  }
}


@-webkit-keyframes arrow-anim {
  0% {
    -webkit-transform: rotate(-45deg) translate(0, 0);
  }
  20% {
    -webkit-transform: rotate(-45deg) translate(-10px, 10px);
  }
  40% {
    -webkit-transform: rotate(-45deg) translate(0, 0);
  }
}

