}
.project-timeline .project-timeline-event {
    border-bottom: 1px dashed rgba(255, 255, 255, 0.1);
    padding-bottom: 25px;
    margin-bottom: 50px;
    position: relative;
}
.project-timeline .project-timeline-event:last-of-type {
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
}
.project-timeline .project-timeline-event:before,
.project-timeline .project-timeline-event:after {
    position: absolute;
    display: block;
    top: 0;
}
.project-timeline .project-timeline-event:before {
    left: -170.5px;
    color: rgba(255, 255, 255, 0.4);
    content: attr(data-date);
    text-align: right;
    font-weight: 100;
    font-size: 0.9em;
    min-width: 120px;
}
.project-timeline .project-timeline-event:after {
    box-shadow: 0 0 0 4px #00aacd;
    left: -27.85px;
    background: #313534;
    border-radius: 50%;
    height: 11px;
    width: 11px;
    content: "";
    top: 5px;
}

