/* CSS Sheets */                         /* for the FAQ accordion*/

/* CSS reset */
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  background: transparent;
  font-family: 'Rubik', sans-serif;
}

.font-size-1em {
  font-size: 1em;
}

.font-size-1pt1em {
  font-size: 1.1em;
}

.font-size-1pt2em {
  font-size: 1.2em;
}

.font-size-2em {
  font-size: 2em;
}

.normal-text {
  font-weight: normal;
}

.bold-text {
  font-weight: bold;
}

.italic-text {
  font-style: italic;
}

.tnr {
  font-family: "Times New Roman", Times, serif !important;
}

.parallax {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
/* turn off parallax scrolling for all tablets and phones */
@media only screen and (max-device-width: 1366px) {
  .parallax {
    background-attachment: scroll;
  }
}

.sticky {
  position: absolute;
  height: 200px;
  width: 200px;
  background: #ffc;
  box-shadow: 0px 0px 5px 0px #888;
  overflow: hidden;
  z-index: 10;
}

.sticky-text {
  position: relative;
  top: 20px;
  left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  color: black;
  font-family: 'Indie Flower';
  outline: none;
}

.sticky-text > a {
  color: navy;
  font-family: 'Indie Flower';
}

.row {
    display: flex;
}

.two-col-format-column {
    flex: 50% 1;
}

.three-col-format-column {
    flex: 33% 1;
}

.text-indent {
    text-indent: 50px;
}

/*********************************** showup ***********************************/

@keyframes showup {
    from {
        opacity: 0%;
    }
    to {
        opacity: 100%;
    }
}
@-webkit-keyframes showup {
    from {
        -webkit-opacity: 0%;
    }
    to {
        -webkit-opacity: 100%;
    }
}

/********************************** slidein **********************************/

@-webkit-keyframes slidein {
    from {
      margin-left: -800px;
    }
    to {
      margin-left: 0px;
    }
}

@keyframes slidein {
    from {
      margin-left: -800px;
    }
    to {
      margin-left: 0px;
    }
}

/********************************** slideup **********************************/

@-webkit-keyframes slideup {
    from {
      margin-top: 800px;
    }
    to {
      margin-top: 0px;
    }
}

@keyframes slideup {
    from {
      margin-top: 800px;
    }
    to {
      margin-top: 0px;
    }
}

/******************************* showup-slideup *******************************/
@keyframes showup-slideup {
    from {
      opacity: 0%;
      margin-top: 800px;
    }
    to {
      margin-top: 0px;
      opacity: 100%;
    }
}
@-webkit-keyframes showup-slideup {
    from {
      -webkit-opacity: 0%;
      margin-top: 800px;
    }
    to {
      -webkit-opacity: 100%;
      margin-top: 0px;
    }
}

